export default {
	data() {
		return {
		}
	},
	props: {
		/**
		 * 输入类型
		 */
		type: String,
		/**
		 * 值
		 */
		value: String,
		/**
		 * 占位符
		 */
		placeholder: String,
		/**
		 * 是否显示清除按钮
		 */
		clearable: {
			type: [Boolean, String],
			default: false,
		}
	},
	methods: {
		clear() {
			this.$emit('input', '')
		},
		display() {
			this.showPassword = !this.showPassword
		},
		onFocus() {
			this.isFocus = true
		},
		onBlur() {
			this.$nextTick(() => {
				this.isFocus = false
			})
		},
		onInput(e) {
			this.$emit('input', e.target.value)
			// console.log(e.target.value)
		}
	},
	computed: {
		clearable_() {
			return String(this.clearable) !== 'false'
		},
		displayable_() {
			return String(this.displayable) !== 'false'
		},
		focus_() {
			return String(this.focus) !== 'false'
		}
	},
}
